body {
  font: 16px/1.4 "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  overflow-x: hidden;
  min-width: 320px; }

.wrapper {
  box-sizing: content-box;
  padding: 0 45px;
  margin: 0 auto;
  position: relative; }

.wrapper-1300 {
  max-width: 1300px; }

.wrapper-1160 {
  max-width: 1160px; }

.wrapper-1100 {
  max-width: 1100px; }

.wrapper-1020 {
  max-width: 1020px; }

.flex-wrapper {
  display: flex;
